.PageWrapper{display:grid;place-items:center;margin-top:10px}.Table{border:1px solid rgba(73,80,87,.4196078431);padding:1.5rem;border-radius:.375rem;width:50%;text-align:center;margin-top:10px}@media screen and (max-width: 767px){.Table{width:90%}}.Table>thead{vertical-align:bottom}.Table tbody,.Table td,.Table tfoot,.Table th,.Table thead,.Table tr{border-color:inherit;border-style:solid;border-width:0}.Table>:not(caption)>*>*{padding:.5rem .5rem;color:initial,initial,#fff;border-bottom-width:1px;box-shadow:inset 0 0 0 9999px initial,initial,rgba(0,0,0,0)}.Table>tbody{vertical-align:inherit}.Table>tbody tr{border-color:inherit;border-style:solid;border-width:0}